@import "../../styles/utils/vars";
@import "../../styles/utils/mixins";

.reviews-main {
  &__content {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    row-gap: 40px;
    justify-content: center;
    align-items: center;
  }

  &__card {
    max-width: 500px;

    &--doshed-bg {
      // padding-top: 50px;
      height: max-content;
      // width: 100%;
      // height: max-content;
      width: 100%;
      height: max-content;
      background-image: url(../../images/doshed_bg_img.png);
      background-size: 100% 100%;
    }

    &--position-1 {
      margin-left: 70px;
    }

    &--position-2 {
      margin-left: 0;
    }
  }
}